@import url(https://fonts.googleapis.com/css?family=Permanent+Marker&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  background: #69a1ad;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.title {

  font-family: 'Permanent Marker', cursive;
  font-size: 4em;
  text-align: center;
}

.container {
		  display : grid;
		  grid-template: 100px 100px 100px / 100px 100px 100px;
		  grid-gap : 5px;
		  padding: 1px;
		  margin : 0 auto;
		  max-width: 303px;
}

.next {

	font-size: 2em;
	text-align: center;
	margin: 15px;
}

.gameEnd {
	font-size: 3em;
	text-align: center;
	margin: 15px;
	font-weight: bolder;

}

.resetBtn {
	box-shadow:inset 0px 1px 0px 0px #f29c93;
	background:-webkit-gradient(linear, left top, left bottom, color-stop(0.05, #fe1a00), color-stop(1, #ce0100));
	background:-webkit-linear-gradient(top, #fe1a00 5%, #ce0100 100%);
	background:-webkit-gradient(linear, left top, left bottom, color-stop(5%, #fe1a00), to(#ce0100));
	background:linear-gradient(to bottom, #fe1a00 5%, #ce0100 100%);
	filter:progid:DXImageTransform.Microsoft.gradient(startColorstr='#fe1a00', endColorstr='#ce0100',GradientType=0);
	background-color:#fe1a00;
	border-radius:6px;
	
	display:inline-block;
	cursor:pointer;
	color:#ffffff;
	font-family:Arial;
	font-size:15px;
	font-weight:bold;
	padding:6px 24px;
	text-decoration:none;
	text-shadow:0px 1px 0px #b23e35;
	
}

.resetWrapper {
	width: 85px;
	margin: auto;		
	padding: 5px;
}

.square {


	  background: -webkit-gradient(linear, left top, right bottom, from(black), to(#69a1ad));


	  background: -webkit-linear-gradient(top left, black, #69a1ad);


	  background: linear-gradient(to bottom right, black, #69a1ad);;
	  padding: 0px;
	  
	  border: 2px solid black;
	  font-size: 3em;
	  text-align: center;
	  font-weight: bolder;
	  /*line-height: 45px;*/
	  margin: 0;
	  border-radius: 6px; 

}


